import './ProductBlock.scss';
import Counter from '../../../components/UI/Counter/Counter';
import { Button } from '../../../components/UI/Button/Button';
import IconVerified from '../../../components/UI/Icon/Icon_verified';
import IconNotVerified from '../../../components/UI/Icon/Icon_not-verified';
import IconScales from '../../../components/UI/Icon/Icon_scales';
import IconHearth from '../../../components/UI/Icon/Icon_hearth';
import IconInfo from '../../../components/UI/Icon/Icon_info';
import ProductRating from '../../../components/ProductRating/ProductRating';
import CharacteristicColor from '../../../components/CharacteristicColor/CharacteristicColor';
import { useEffect, useState } from 'react';
import CommentsBlock from '../../../components/CommentsBlock/CommentsBlock';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import { Navigation } from 'swiper/modules';
import 'swiper/css/navigation';

export default function ProductBlock({ product }) {
  const defaultImage = product.images[0];
  const [mainImage, setMainImage] = useState(defaultImage);

  const handleImageClick = (event) => {
    setMainImage(event.target.src);
  };

  return (
    <section className="product-block">
      <div className="product-block__top">
        <div className="images">
          <Swiper
            spaceBetween={40}
            slidesPerView={4}
            direction={'vertical'}
            navigation={true}
            modules={[Navigation]}
            className="images__column"
          >
            {product.images.map((image, idx) => (
              <SwiperSlide key={idx} className="images__slide">
                <img
                  key={idx}
                  className="images__item"
                  src={image}
                  alt=" "
                  onClick={handleImageClick}
                />
              </SwiperSlide>
            ))}
          </Swiper>
          <img className="images__main" src={mainImage} alt="  " />
        </div>

        <div className="info">
          <div className="info__title-line">
            <h2 className="info__title">{product.title}</h2>
            <ProductRating rating={product.rating} />
          </div>

          <div className="info__shipper">
            <p className="info__shipper-name">{product.shipper}</p>
            <IconInfo
              className="info__shipper-icon hint-right-middle"
              data-hint={product.shipper}
            />
          </div>

          <p className="info__code">{`. ${product.productCode}`}</p>

          <div className="info__status-line">
            {product.availableStatus ? (
              <>
                <IconVerified />
                <p className="info__status"> </p>
              </>
            ) : (
              <>
                <IconNotVerified />
                <p className="info__status">  </p>
              </>
            )}
          </div>

          <div className="info__size-line">
            <p className="info__size"></p>
          </div>

          <div className="info__color-line">
            <p className="info__color"></p>
            <CharacteristicColor characteristicValue={product.colors} />
          </div>
        </div>

        <div className="order">
          <div className="order__price">
            <h3 className="order__price-value">
              {`${product.price} `} &#x20bd;
              <div className="order__icons">
                <IconScales />
                <IconHearth />
              </div>
            </h3>
            <p className="order__price-piece">{` . ${product.price} `} &#x20bd;</p>

            <div className="order__price-quantity">
              <div
                className="order__price-quantity-now"
                style={{ width: (product.piecesNow / product.piecesAll) * 100 + '%' }}
              />
            </div>

            <p className="order__price-remainder">
              :{' '}
              <span className="order__price-remainder-now">{`${product.piecesNow} .`}</span>
            </p>

            <p className="order__price-min-order">{`    : ${product.minOrder}`}</p>
          </div>

          <div className="order__delivery">
            <p className="order__delivery-title"> </p>
            <p className="order__delivery-subtitle">
                   <span className="order__delivery-data"></span>
            </p>
            <p className="order__delivery-subtitle">
                <span className="order__delivery-data">10 </span>
            </p>
          </div>

          <Counter
            initCount={product.minOrder}
            minValue={product.minOrder}
            onChangeProductQuantity={(count) => {}}
          />
          <Button size="xl" mode="secondary" extraClass="order__button">
             
          </Button>
        </div>
      </div>

      <div className="product-block__bottom">
        <div className="description">
          <div className="description__item">
            <h4 className="description__title"></h4>
            <p className="description__text">
                  Urbano  
               .   ,   .
                     . 
                .  .     .
                 15,4,   4, , , ,
              ,    .   . 
              ,     .    
              ,  ,     . 
              423012  ,   
              .  ,    
            </p>
          </div>
          <div className="description__item">
            <h4 className="description__title"></h4>
            <p className="description__text">
                  Urbano  
               .   ,   .
                     . 
                .  .     .
                 15,4,   4, , , ,
              ,    .   . 
              ,     .    
              ,  ,     . 
              423012  ,   
              .  ,    
            </p>
          </div>
        </div>

        <div className="comments">
          <h3 className="comments__title">
            
            <p className="comments__count">15</p>
          </h3>
          <div className="comments__container">
            <CommentsBlock
              author=" ."
              rating={4.8}
              text=" .     ,  ... . . . . ."
            />
            <CommentsBlock
              author=" ."
              rating={4.8}
              text=" .     ,  ... . . . . ."
            />
            <CommentsBlock
              author=" ."
              rating={4.8}
              text=" .     ,  ... . . . . ."
            />
            <CommentsBlock
              author=" ."
              rating={4.8}
              text=" .     ,  ... . . . . ."
            />
            <CommentsBlock
              author=" ."
              rating={4.8}
              text=" .     ,  ... . . . . ."
            />
            <CommentsBlock
              author=" ."
              rating={4.8}
              text=" .     ,  ... . . . . ."
            />
          </div>
          <Button size="xl" mode="secondary">
             
          </Button>
        </div>
      </div>
    </section>
  );
}
